body {margin:0 0;width:100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1{margin:1%}
h2{color:#06AFE3}

.body{position:absolute;width:100%;overflow-x:hidden;top:3%;height:97%;background:black;color:white}
.navbar{position:fixed;top:0;width:100%;height:5%;display:grid;grid-template-columns:repeat(5,1fr);background:black;z-index:2}
/* .navbar:after{content:'';position:absolute;width:100%;height:10px;top:100%;background-image:linear-gradient(black,transparent);z-index:1} */
.navbar{position:fixed;top:0;width:100%;height:5%;display:grid;grid-template-columns:repeat(5,1fr);background-image:linear-gradient(black, black,black,black,transparent);z-index:1}

.navbar a{position:relative;font-size:0.9em;color:lightgray;text-decoration:none;width:100%;transition:0.25s;-webkit-transition:0.25s ease;}
.navbar a:hover{color:white;transition:0.25s}
.navbar a span{position:absolute;bottom:33%;width:100%;height:40%;text-align:center;transition:0.25s}

/* .navbar a:hover,.navbar a.active:hover{background:linear-gradient(gray,#06AFE3,#06AFE3);transition:1s} */
/* .navbar a:hover{transition:1s;border-bottom:1px solid #06AFE3;transition:1s} */
/* .navbar a:hover span{text-shadow: 2px 2px 2px #06AFE3;transition:1s} */
.underbar{height:0px;background:#06AFE3;top:90%;position:absolute;-webkit-transition:0.5s ease}
.navbar a:nth-of-type(1):hover ~ .underbar{left:20%;width:20%;background:#06AFE3;height:4px}
.navbar a:nth-of-type(2):hover ~ .underbar{left:40%;width:20%;background:#06AFE3;height:4px}
.navbar a:nth-of-type(3):hover ~ .underbar{left:60%;width:20%;background:#06AFE3;height:4px}
.navbar a:nth-of-type(4):hover ~ .underbar{left:80%;width:20%;background:#06AFE3;height:4px}
/* .navbar a.active:hover span{bottom:21.5%;font-size:1em;transition:1s} */

/* .navbar a.active{background:linear-gradient(gray,gray,#06AFE3);font-weight:bold;transition:1s} */

/* ENTRANCE */
.fullNameContainer{position:fixed;height:50%;width:100%;background:white;font-size:2em;z-index:1}
.fullNameContainer .full{background:transparent;position:absolute;bottom:10%;left:5%;width:200%;font-size:0.9em}
.fullNameContainer .full .name{position:relative;margin-right:0.25%;background:none}
.fullNameContainer .full .name span{color:#06AFE3;background:none}
.fullNameContainer .full .first{left:100%;
  -webkit-animation: slide 1s ease 0.5s forwards;
  -moz-animation: slide 1s ease 0.5s forwards;
  -o-animation: slide 1s ease 0.5s forwards;
  -ms-animation: slide 1s ease 0.5s forwards;
  animation: slide 1s ease 0.5s forwards}
.fullNameContainer .full .middle{left:90%;
  -webkit-animation: slide 1s ease 1s forwards;
  -moz-animation: slide 1s ease 1s forwards;
  -o-animation: slide 1s ease 1s forwards;
  -ms-animation: slide 1s ease 1s forwards;
  animation: slide 1s ease 1s forwards}
.fullNameContainer .full .last{left:70%;
  -webkit-animation: slide 1s ease 1.5s forwards;
  -moz-animation: slide 1s ease 1.5s forwards;
  -o-animation: slide 1s ease 1.5s forwards;
  -ms-animation: slide 1s ease 1.5s forwards;
  animation:slide 1s ease 1.5s forwards}
.fullNameContainer .full .suffix{left:60%;
  -webkit-animation: slide 1s ease 2s forwards;
  -moz-animation: slide 1s ease 2s forwards;
  -o-animation: slide 1s ease 2s forwards;
  -ms-animation: slide 1s ease 2s forwards;
  animation:slide 1s ease 2s forwards}

.footerContainer{background:#06AFE3;bottom:0;position:fixed;margin:0;width:100%;height:50%;}
.footerContainer .footer{background:#06AFE3;color:white;width:200%;list-style:none;font-size:1.5em;position:absolute;padding:0;left:5%}
.footerContainer .footer li{padding:1% 0;position:relative;left:100%;background:none;color:black}
.footerContainer .footer li.li0{
  -webkit-animation: slide 1s ease 0.5s forwards;
  -moz-animation: slide 1s ease 0.5s forwards;
  -o-animation: slide 1s ease 0.5s forwards;
  -ms-animation: slide 1s ease 0.5s forwards;
  animation: slide 1s ease 0.5s forwards}
.footerContainer .footer li.li1{
  -webkit-animation: slide 1s ease 1s forwards;
  -moz-animation: slide 1s ease 1s forwards;
  -o-animation: slide 1s ease 1s forwards;
  -ms-animation: slide 1s ease 1s forwards;
  animation: slide 1s ease 1s forwards}
.footerContainer .footer li.li2{
  -webkit-animation: slide 1s ease 1.5s forwards;
  -moz-animation: slide 1s ease 1.5s forwards;
  -o-animation: slide 1s ease 1.5s forwards;
  -ms-animation: slide 1s ease 1.5s forwards;
  animation: slide 1s ease 1.5s forwards}
.footerContainer .footer li.li3{
  -webkit-animation: slide 1s ease 2s forwards;
  -moz-animation: slide 1s ease 2s forwards;
  -o-animation: slide 1s ease 2s forwards;
  -ms-animation: slide 1s ease 2s forwards;
  animation: slide 1s ease 2s forwards}

@-webkit-keyframes hide{from{visibility:hidden}to{visibility:hidden}}
@-moz-keyframes hide{from{visibility:hidden}to{visibility:hidden}}
@-ms-keyframes hide{from{visibility:hidden}to{visibility:hidden}}
@-o-keyframes hide{from{visibility:hidden}to{visibility:hidden}}
  @keyframes hide{from{visibility:hidden}to{visibility:hidden}}
@-webkit-keyframes slide{to{left:0%;position:relative}}
@-moz-keyframes slide{to{left:0%;position:relative}}
@-ms-keyframes slide{to{left:0%;position:relative}}
@-o-keyframes slide{to{left:0%;position:relative}}
@keyframes slide{to{left:0%;position:relative}}

.fullNameContainer .full .rest{
  -webkit-animation: shrink 1s ease 4s forwards;
  -moz-animation: shrink 1s ease 4s forwards;
  -o-animation: shrink 1s ease 4s forwards;
  -ms-animation: shrink 1s ease 4s forwards;
  animation:shrink 1s ease 4s forwards}

@-webkit-keyframes shrink{to{font-size:0px;display:none}}
@-moz-keyframes shrink{to{font-size:0px;display:none}}
@-ms-keyframes shrink{to{font-size:0px;display:none}}
@-o-keyframes shrink{to{font-size:0px;display:none}}
  @keyframes shrink{to{font-size:0px;display:none}}

.fullNameContainer .full{
  -webkit-animation: shrinkHeaderName 1s ease 4.5s forwards;
  -moz-animation: shrinkHeaderName 1s ease 4.5s forwards;
  -o-animation: shrinkHeaderName 1s ease 4.5s forwards;
  -ms-animation: shrinkHeaderName 1s ease 4.5s forwards;
  animation:shrinkHeaderName 1s ease 4.5s forwards;}
.fullNameContainer{
  -webkit-animation: shrinkHeader 2s ease 4s forwards;
  -moz-animation: shrinkHeader 2s ease 4s forwards;
  -o-animation: shrinkHeader 2s ease 4s forwards;
  -ms-animation: shrinkHeader 2s ease 4s forwards;
  animation:shrinkHeader 2s ease 4s forwards}

@-webkit-keyframes shrinkHeader{50%{background:transparent;height:50%;width:100%}100%{height:5%;width:20%;background:transparent;z-index:1;top:-3px}}
@-moz-keyframes shrinkHeader{50%{background:transparent;height:50%;width:100%}100%{height:5%;width:20%;background:transparent;z-index:1;top:-3px}}
@-ms-keyframes shrinkHeader{50%{background:transparent;height:50%;width:100%}100%{height:5%;width:20%;background:transparent;z-index:1;top:-3px}}
@-o-keyframes shrinkHeader{50%{background:transparent;height:50%;width:100%}100%{height:5%;width:20%;background:transparent;z-index:1;top:-3px}}
@keyframes shrinkHeader{50%{background:transparent;height:50%;width:100%}100%{height:5%;width:20%;background:transparent;z-index:1;top:-3px}}
@-webkit-keyframes shrinkHeaderName{to{width:90%;background:transparent;}}
@-moz-keyframes shrinkHeaderName{to{width:90%;background:transparent;}}
@-ms-keyframes shrinkHeaderName{to{width:90%;background:transparent;}}
@-o-keyframes shrinkHeaderName{to{width:90%;background:transparent;}}
@keyframes shrinkHeaderName{to{width:90%;background:transparent;}}

.footerContainer{
  -webkit-animation: shrinkFooterContainer 1s ease 5s forwards;
  -moz-animation: shrinkFooterContainer 1s ease 5s forwards;
  -o-animation: shrinkFooterContainer 1s ease 5s forwards;
  -ms-animation: shrinkFooterContainer 1s ease 5s forwards;
  animation:shrinkFooterContainer 1s ease 5s forwards}

@-webkit-keyframes shrinkFooterContainer{to{height:0%}}
@-moz-keyframes shrinkFooterContainer{to{height:0%}}
@-ms-keyframes shrinkFooterContainer{to{height:0%}}
@-o-keyframes shrinkFooterContainer{to{height:0%}}
  @keyframes shrinkFooterContainer{to{height:0%}}

/* ENTRANCE */

.about{position:relative;padding-top:5%;}
.about p{text-align:justify}
.about p,.about h3{margin:5% 3%}
p a{color:white}
.contact{position:relative;height:95%;padding-top:10%}
.contact p{margin-left:3%;}

.resume{position:relative;height:90%;background:white;color:black;}

.resume a{position:absolute;top:1%;right:1%;color:white;transition:0.25s;background:#06AFE3;border-radius:5px;padding:5px;text-decoration:none;border:1px solid #06AFE3}
.resume a:hover{color:#06AFE3;background:white;transition:0.25s}

