.iframe-holder{height:95%;position:relative;padding:10px;overflow-y:scroll}
.resume iframe{width:145%;left:-23%;right:-24%;height:1px;min-height:90%;max-height:90%;position:relative;border:none;margin:none}
div.socialMedia{text-align:center}
.contact a img{position:relative;height:100px;margin:3%}

.sideScroll{height:90%;width:100%;overflow-x:scroll;overflow-y:hidden;position:relative;white-space:nowrap;-webkit-scroll-snap-type: mandatory;-webkit-scroll-snap-destination: 50% 50%;scroll-snap-type:x mandatory;padding:0;margin:0;bottom:0;}
.portfolio{position:relative;height:90%;padding-top:1%}
.portfolioItem{position:relative;display:inline-block;border:1px solid white;width:90%;height:96%;min-height:90%;-webkit-scroll-snap-coordinate: 50% 50%;scroll-snap-align:center;white-space:normal;vertical-align:top;margin:1%;transition:transform .5s;overflow-y:scroll;transition:1s;border-radius:5px;padding:1%}
.portfolioItem:hover{transform:scaleX(1.01);transition:0.5s}

.portfolioItem p{padding:1% 1%;margin:0}
.portfolioItem .iframe-container{position:relative;width:100%;height:0;padding-bottom:56.25%;max-width:700px;}
.portfolioItem iframe{position:absolute;width:100%;height:100%;max-height:500px}

.portfolioTopRow{display:grid;grid-template-columns:1fr 2fr;height:75px}
.portfolioTopRow h2{padding:0px;margin:10px}
.linkCard{margin:1px;width:auto;float:right;width:65px;transition:1s;height:60px;border:1px solid white;border-radius:5px}
.linkCard:hover{transform:scale(1.1);transition:1s}
.linkCard:hover div p:nth-child(2){color:#06AFE3;transition:0.5s}
.linkCard a{text-decoration:none}
.linkCard a div{position:relative;max-width:300px}
.linkCard a div div{display:block;text-align:center;margin:auto;height:25px}
p.logo{padding:0;margin:0;color:white;font-size:20px}
img.logo{margin-top:2px;height:20px;}
.linkCard div p:not(:first-child){font-size:14px;color:white;text-align:center}
.linkCard div p:nth-child(3){font-size:10px}
